import { Navbar } from '../components/Navbar';
import { Ghost } from 'lucide-react';
import { AuthProvider } from '../context/AuthContext';
import { CartProvider } from '../context/CartContext';

export function RootLayout({ children }: { children: React.ReactNode }) {
  return (
    <AuthProvider>
      <CartProvider>
        <div className="min-h-screen flex flex-col font-sans bg-cream text-stone-800">
          <Navbar />
          <main className="flex-grow">
            {children}
          </main>
          <footer className="bg-stone-900 text-stone-400 py-8 mt-auto">
            <div className="container mx-auto px-4 text-center">
              <div className="flex items-center justify-center mb-4 select-none">
                <span className="text-fern mr-2 text-2xl"></span>
                <span className="font-serif text-lg text-stone-200">Piaa Online</span>
              </div>
              <p className="text-sm"> {new Date().getFullYear()} Piaa Online. Toate drepturile rezervate.</p>
            </div>
          </footer>
        </div>
      </CartProvider>
    </AuthProvider>
  );
}
